/* ==========================================================================
   WooCommerce Product List page responsive styles - begin
   ========================================================================== */

@include laptop-landscape-large {
    
    .woocommerce {
        
        $product_list_vc_columns: ('columns-6');
        
        @for $i from 0 to length($product_list_vc_columns) {
            &.#{nth($product_list_vc_columns,$i+1)} {
                $column_vc_width: 100%/5;
                
                > .products {
                    
                    > .product {
                        width: $column_vc_width;
                        
                        @media only screen and (min-width: $laptop-landscape-plus-pixel) {
                            
                            &:nth-child(5n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}

@include laptop-landscape {
    
    .woocommerce {
        
        $product_list_vc_columns: ('columns-5', 'columns-6');
        
        @for $i from 0 to length($product_list_vc_columns) {
            &.#{nth($product_list_vc_columns,$i+1)} {
                $column_vc_width: 100%/4;
                
                > .products {
                    
                    > .product {
                        width: $column_vc_width;
                        
                        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
                            
                            &:nth-child(4n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}

@include ipad-landscape {
    
    .mkdf-pl-main-holder,
    .related.products,
    .upsells.products,
    .cross-sells {
        
        $product_list_columns: ( 'columns-3', 'columns-4');
        
        @for $i from 0 to length($product_list_columns) {
            
            > .products {
                
                > .product {
                    
                    .mkdf-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        $column_width: 100%/3;
                        width: $column_width;
                        
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(3n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }

    .mkdf-woocommerce-columns-2 .mkdf-pl-main-holder > .products > .product {
        width: 50%;

        &:nth-child(2n+1) {
            clear: both;
        }
    }

    body .select2-container--default .select2-selection--single {
        text-align: left;

        .select2-selection__rendered {
            padding: 0;
        }
    }
    
    .woocommerce {
        
        $product_list_vc_columns: ('columns-4', 'columns-5', 'columns-6');
        
        @for $i from 0 to length($product_list_vc_columns) {
            &.#{nth($product_list_vc_columns,$i+1)} {
                $column_vc_width: 100%/3;
                
                > .products {
                    
                    > .product {
                        width: $column_vc_width;
                        
                        @media only screen and (min-width: $ipad-portrait-plus-pixel) {
                            
                            &:nth-child(3n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }

    $columns_space_label: ('huge');
    $columns_space_width: (30);

    @for $i from 0 to length($columns_space_label) {
        &.mkdf-woo-#{nth($columns_space_label,$i+1)}-space {
            $column_width: nth($columns_space_width,$i+1);
            $column_width_margin: $column_width * 2;

            ul.products {
                @if($column_width == 0) {
                    margin: 0;
                } @else {
                    margin: 0 -#{$column_width}px;
                }

                > .product {
                    padding: 0 #{$column_width}px;
                    margin: 0 0 #{$column_width_margin}px;
                }
            }
        }
    }
}

@include ipad-portrait {
    
    .mkdf-pl-main-holder,
    .related.products,
    .upsells.products,
    .cross-sells {
        
        $product_list_columns: ('columns-3', 'columns-4');
        
        @for $i from 0 to length($product_list_columns) {
            
            > .products {
                
                > .product {
                    
                    .mkdf-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: 50%;
                        
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(2n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
    
    .woocommerce {
        
        $product_list_vc_columns: ('columns-3', 'columns-4', 'columns-5', 'columns-6');
        
        @for $i from 0 to length($product_list_vc_columns) {
            &.#{nth($product_list_vc_columns,$i+1)} {
                
                > .products {
                    
                    > .product {
                        width: 50%;
                        
                        @media only screen and (min-width: $phone-landscape-plus-pixel) {
                            
                            &:nth-child(2n+1) {
                                clear: both;
                            }
                        }
                    }
                }
            }
        }
    }
}

@include phone-landscape {
    
    .mkdf-pl-main-holder,
    .related.products,
    .upsells.products,
    .cross-sells {
        
        $product_list_columns: ('columns-2', 'columns-3', 'columns-4');
        
        @for $i from 0 to length($product_list_columns) {
            
            > .products {
                
                > .product {
                    
                    .mkdf-woocommerce-#{nth($product_list_columns,$i+1)} & {
                        width: 100%;
                    }
                }
            }
        }
    }
    
    .woocommerce {
        
        $product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
        
        @for $i from 0 to length($product_list_vc_columns) {
            &.#{nth($product_list_vc_columns,$i+1)} {
                
                > .products {
                    
                    > .product {
                        width: 100%;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Product List page responsive styles - end
   ========================================================================== */