@include ipad-landscape {
    .widget.woocommerce {
        &.widget_products {
            ul {
                li {
                    a img {
                        max-width: 45%;
                    }
                }
            }
        }
        &.widget_price_filter {
            .price_slider_wrapper .ui-widget-content {
                width: 60%;
                margin: 0 auto;
            }
        }
    }
}