/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include ipad-portrait {
	
	.mkdf-woocommerce-page {
		
		table.cart {
			
			td.actions {
				
				.coupon {
					
					input[type="text"] {
						width: 180px;
						margin: 0 17px 0 0;
					}
				}

				.button {
					margin-left: 14px;
				}
			}
		}
	}
}

@include phone-landscape {
	
	.mkdf-woocommerce-page {
		
		table.cart {

			thead tr th {
				padding: 10px;
				font-size: 10px;

				&.product-remove,
				&.product-thumbnail {
					display: none;
				}
			}
			tr {
				
				th, td {
					font-size: 12px;
				}
				
				&.cart_item {
					
					td {

						&.product-remove,
						&.product-thumbnail {
							display: none;
						}
					}
				}
			}
			
			td.actions {
				
				.coupon {
					margin: 0 0 20px;
					
					input[type="text"] {
						width: 120px;
						margin: 0 10px 20px 0;
					}
				}
				
				.coupon,
				> input {
					float: none;
					display: block;
				}

				.button {
					margin: 0;
				}

				>.button {
					float: left;
				}
			}

			.mkdf-quantity-buttons .mkdf-quantity-minus,
			.mkdf-quantity-buttons .mkdf-quantity-plus {
				display: none;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */